<template>
  <page
    @on-ready="onReady"
    ref="page"
    class="fault-page common-page"
    :headerHeightVW="headerHeightVW"
    scrollbar
  >
    <van-nav-bar slot="header" id="header" title="相关故障" left-arrow @click-left="onClickLeft">
    </van-nav-bar>

    <div v-if="detail.id">
      <van-cell-group>
        <van-cell title="故障编号：">{{detail.no}}</van-cell>
      </van-cell-group>

      <van-cell-group>
        <van-cell title="故障来源：">{{detail.source}}</van-cell>
        <van-cell title="故障专业：">{{detail.profession}}</van-cell>
        <!-- <van-cell title="系统模块：">{{detail.systemMode}}</van-cell> -->
        <van-cell title="故障设备：">{{(detail.deviceCategory ? detail.deviceCategory + ' / ' : '') + detail.device}}</van-cell>
        <van-cell title="故障代码：">{{detail.code && detail.code}}</van-cell>
        <van-cell title="故障地点：">{{detail.line}} / {{detail.station}}</van-cell>
        <van-cell title="详细地址：">{{detail.address}}</van-cell>
        <!-- <van-cell title="故障类型：">{{detail.type}}</van-cell> -->
        <van-cell title="故障等级：">{{detail.level}}</van-cell>
        <van-cell title="故障分类：">{{detail.reason}}</van-cell>
        <van-cell title="故障时间：">{{detail.time}}</van-cell>
      </van-cell-group>

      <van-cell-group>
        <van-cell title="报告人：">{{detail.reportUser}}</van-cell>
        <van-cell title="报告时间：">{{detail.reportTime}}</van-cell>
      </van-cell-group>

      <van-cell-group>
        <van-cell class="detail-title" title="故障现象：" />
        <van-cell>
          <span style="color: #969799;">{{detail.phenomenon}}</span>
        </van-cell>
      </van-cell-group>

      <van-cell-group>
        <van-cell class="detail-title" title="维修建议：" />
        <van-cell>
          <span style="color: #969799;">{{detail.suggest}}</span>
        </van-cell>
      </van-cell-group>

      <van-cell-group>
        <van-cell class="detail-title" title="图片：" />
        <div class="images-container">
          <div class="image-item-show" v-for="(item, index) in detail.images" :key="index" @click="handlePlayImages(index)">
            <!-- <img :src="item.url" @click="handlePlayImages(index)"> -->
            <auto-image :src="item.url" :width="108" :height="108" style="margin-bottom: 12px;margin-left: 12px;"></auto-image>
          </div>
        </div>
      </van-cell-group>

      <van-cell-group>
        <van-cell class="detail-title" title="视频：" />
        <div class="images-container">
          <div class="image-item-show"  v-for="(item, index) in detail.videos" :key="index" @click="handlePlayVideo(item)">
            <!-- <auto-image :src="item.url" :width="108" :height="108" style="margin-bottom: 12px;margin-left: 12px;"></auto-image> -->
            <img src="../../assets/images/video-default.png" class="img-video" />
          </div>
        </div>
      </van-cell-group>

      <van-cell-group>
        <van-cell :title="'工单 '+ (!item.no ? '无' : item.no)" is-link clickable v-for="item in detail.orders" :key="item.id" @click="handleOrder(item)"></van-cell>
      </van-cell-group>
    </div>

    <div class="blank-empty"></div>
  </page>
</template>

<script>
import Page from '../../components/Page'
import io from '../../api'
import navigate from '../../utils/navigate.js'
import AutoImage from '../../components/AutoImage'

export default {
  components: {
    Page,
    AutoImage
  },
  data: () => ({
    headerHeightVW: 0,
    id: null,
    detail: {}
  }),
  methods: {
    onReady (header, main) {
      this.headerHeightVW = (header.h + 1) * 100 / header.w

      this.id = navigate.getParam('id')

      this.loadDetail()
    },

    loadDetail () {
      this.$toast.loading({
        duration: 0,
        loadingType: 'spinner'
      })

      io.getFaultDetail({
        id: this.id
      }).then(data => {
        this.detail = data

        this.$toast.clear()
      }).catch(msg => {
        this.$toast(msg)
      })
    },

    handlePlayVideo (item) {
      // alert(item.url)
      api.openVideo({
        url: item.url
      })
      // navigate.open('videoViewer', {url: item.url})
    },

    handlePlayImages (index) {
      localStorage.setItem('photos', JSON.stringify(this.detail.images))
      navigate.open('photoViewer', {index: index})
    },

    onClickLeft () {
      navigate.back()
    },

    handleOrder (item) {
      navigate.open('orderdetail', {id: item.id})
    }
  }
}
</script>
